.example-appear {
  opacity: 0.01;
}

.example-appear.example-appear-active {
  opacity: 1;
  transition: opacity 0.3s ease-in;
}

html {
  padding: 0px 0px 50px 0px;
  margin: 0px;
  background-color: #141414;
  color: white;
}

input {
  height: 1.3em;
  border-style: solid;
  border-width: 1px;
  border-color: white;
  background-color: black;
  color: grey;
  width: 100%;
  max-width: 40em;
  padding-left: 0.4em;
}

/*Home CSS
=========================================*/
.home {
}

/*Movie Page CSS
========================================*/

.moviePage {
  color: white;
  position: fixed;
  padding: 1em 0%;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: stretch;
  min-width: 100%;
  overflow-y: scroll;
  height: 100%;
}

.moviePageContent {
  padding: 5% 5% 10% 5%;
  flex-grow: 2;
  max-width: 50%;
  max-height: 90%;
}

.moviePageContent h1 {
  font-size: 3em;
  padding-left: 0;
}

.moviePageContent h3 {
  font-style: none;
  font-style: italic;
}

.moviePageContent h4 {
  padding-top: 0;
}

.moviePageContent p {
  font-size: 1em;
  font-style: italic;
  border-radius: 1em;
  padding: 0.5em;
}

.movieThumbnail {
  margin-right: 0.5em;
  width: 500px;
  max-width: 90%;
}

.movieThumbnail img {
  height: auto;
  width: inherit;
  flex-shrink: 1;
}

.genreTag {
  color: black;
  font-size: 0.8em;
  background-color: lightgreen;
  border-radius: 1em;
  padding: 0.4em 1em;
  margin: 0.2em;
}

.moviePageIconLink {
  max-width: 50px;
  display: inline;
}

.moviePageIcon {
  filter: invert(100%);
  max-width: 50px;
  margin: 2em 1em;
  padding: 0;
}

.moviePageIcon:hover {
  filter: invert(50%);
}
/*HireMe page CSS
=====================================*/

.hireMe {
  margin: 3em;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100% auto;
  height: 400px;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
}

.hireMe img {
  margin: 2em 2em 2em 4em;
  height: 250px;
  -webkit-box-shadow: 2px 4px 42px -12px rgba(0, 0, 0, 0.7);
  -moz-box-shadow: 2px 4px 42px -12px rgba(0, 0, 0, 0.7);
  box-shadow: 2px 4px 42px -12px rgba(0, 0, 0, 0.7);
  border-radius: 250px 250px 250px 250px;
  -moz-border-radius: 250px 250px 250px 250px;
  -webkit-border-radius: 250px 250px 250px 250px;
  border: 0px solid #000000;
}

.about {
  margin: 3em;
  background-color: red;
  padding: 1em;
  flex-grow: 2;
  max-width: 50%;
  -webkit-box-shadow: 2px 4px 42px -12px rgba(0, 0, 0, 0.7);
  -moz-box-shadow: 2px 4px 42px -12px rgba(0, 0, 0, 0.7);
  box-shadow: 2px 4px 42px -12px rgba(0, 0, 0, 0.7);
  border-radius: 11px 11px 11px 11px;
  -moz-border-radius: 11px 11px 11px 11px;
  -webkit-border-radius: 11px 11px 11px 11px;
  border: 0px solid #000000;
  height: 200px;
}

/*Stream CSS
================================================*/
.movieCarousel {
  height: auto;
  min-height: 173px;
  text-align: center;
  display: flex;
  flex: 1 1 500px;
  list-style: none;
  padding: 0;
  margin: 0px;
  overflow: hidden;
  align-items: flex-center;
}

.streamCategory {
  margin-left: 1em;
}

ul {
  display: flex;
}

/*MovieCard CSS
============================================*/

.movieCard {
  height: 98%;
  width: 100%;
  margin: 0px 20px;
  transition: all 0.2s ease-in-out;
  position: relative;
}

.movieCardGradient {
  height: 100%;
  width: 100%;
  display: inline-block;
  position: absolute;
  background: -moz-linear-gradient(
    top,
    rgba(0, 0, 0, 0) 0%,
    rgba(0, 0, 0, 0.65) 100%
  );
  background: -webkit-linear-gradient(
    top,
    rgba(0, 0, 0, 0) 0%,
    rgba(0, 0, 0, 0.65) 100%
  );
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0) 0%,
    rgba(0, 0, 0, 0.65) 100%
  );
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#a6000000',GradientType=0 );
}

.movieCardTitle {
  font-style: bold;
  position: absolute;
  bottom: 0.25em;
  left: 0.25em;
  color: lightgrey;
}

.movieCard:hover {
  transform: scale(1.1);
  z-index: 999;
  overflow: visible;
}

.thumbnail {
  width: 100%;
  height-min: 50px;
}

.thumbnail img {
}

